export default {
  value: SWORDMAN,
  label: '',
  children: [{
    value: KNIGHT,
    type: SECOND,
    label: '',
  }, {
    value: CRUSADER,
    type: SECOND,
    label: '',
  }, {
    value: LORD_KNIGHT,
    type: TRANSCENDENT_SECOND,
    label: '',
  }, {
    value: PALADIN,
    type: TRANSCENDENT_SECOND,
    label: '',
  }, {
    value: RUNE_KNIGHT,
    type: THIRD,
    label: '',
  }, {
    value: ROYAL_GUARD,
    type: THIRD,
    label: '',
  }],
};



