import { SECOND, TRANSCENDENT_SECOND, THIRD } from './classNames';
import { MERCHANT, BLACKSMITH, ALCHEMIST, WHITESMITH, CREATOR, MECHANIC, GENETIC } from './classNames';

export default {
  value: MERCHANT,
  label: '',
  children: [{
    value: BLACKSMITH,
    type: SECOND,
    label: '',
  }, {
    value: ALCHEMIST,
    type: SECOND,
    label: '',
  }, {
    value: WHITESMITH,
    type: TRANSCENDENT_SECOND,
    label: '',
  }, {
    value: CREATOR,
    type: TRANSCENDENT_SECOND,
    label: '',
  }, {
    value: MECHANIC,
    type: THIRD,
    label: '',
  }, {
    value: GENETIC,
    type: THIRD,
    label: '',
  }],
};



