import { SECOND, TRANSCENDENT_SECOND, THIRD } from './classNames';
import { ACOLYTE, PRIEST, MONK, HIGH_PRIEST, CHAMPION, ARCH_BISHOP, SURA } from './classNames';

export default {
  value: ACOLYTE,
  label: '',
  children: [{
    value: PRIEST,
    type: SECOND,
    label: '',
  }, {
    value: MONK,
    type: SECOND,
    label: '',
  }, {
    value: HIGH_PRIEST,
    type: TRANSCENDENT_SECOND,
    label: '',
  }, {
    value: CHAMPION,
    type: TRANSCENDENT_SECOND,
    label: '',
  }, {
    value: ARCH_BISHOP,
    type: THIRD,
    label: '',
  }, {
    value: SURA,
    type: THIRD,
    label: '',
  }],
};



