import { SECOND, TRANSCENDENT_SECOND, THIRD } from './classNames';
import { MAGE, WIZARD, SAGE ,HIGH_WIZARD, PROFESSOR, WARLOCK, SORCERER } from './classNames';

export default {
  value: MAGE,
  label: '',
  children: [{
    value: WIZARD,
    type: SECOND,
    label: '',
  }, {
    value: SAGE,
    type: SECOND,
    label: '',
  }, {
    value: HIGH_WIZARD,
    type: TRANSCENDENT_SECOND,
    label: '',
  }, {
    value: PROFESSOR,
    type: TRANSCENDENT_SECOND,
    label: '',
  }, {
    value: WARLOCK,
    type: THIRD,
    label: '',
  }, {
    value: SORCERER,
    type: THIRD,
    label: '',
  }],
};



