import React from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import styled from 'styled-components';
import { Row, Col, Card, Select, InputNumber, Radio } from 'antd';
import { find } from 'lodash';

import {
  updateAspdWeaponId,
  updateAspdLefthandId,
  updateAspdEquipMod,
  updateAspdEquipFixed,
  updateAspdSkillMod,
  updateAspdPotionMod,
} from '../actions';
import weapons from '../constants/weapons';
import aspdTable from '../constants/aspdTable';

const RadioButton = Radio.Button;
const RadioGroup = Radio.Group;
const { Option } = Select;

const aspdUpPotions = [
  { key: 0, name: '', aspdUp: 0 },
  { key: 1, name: '', aspdUp: 10 },
  { key: 2, name: '', aspdUp: 15 },
  { key: 3, name: '', aspdUp: 20 },
];

const Label = styled.div`
  display: inline-block;
  margin-right: 10px;
  font-weight: bold;
  line-height: 32px;
`;

const InputField = styled.div`
  margin-bottom: 10px;
`;

const AspdSetting = ({
  aspd,
  usableWeapons,
  usableLefthand = [],
  updateAspdWeaponId,
  updateAspdLefthandId,
  updateAspdEquipMod,
  updateAspdEquipFixed,
  updateAspdSkillMod,
  updateAspdPotionMod,
}) => (
  <Card title="ASPD Setting" style={{ marginTop: 15 }}>
    <InputField>
      <Row>
        <Col xs={12}>
          <Label></Label>
          <Select style={{ width: 100 }} value={aspd.weaponId} onChange={updateAspdWeaponId}>
            {usableWeapons.map(({ id, baseAspd }) => (
              <Option key={id} value={id}>{find(weapons, { id }).name}</Option>)
            )}
          </Select>
        </Col>
        <Col xs={12}>
          <Label></Label>
          <Select
            style={{ width: 100 }}
            value={aspd.lefthandId}
            onChange={updateAspdLefthandId}
            disabled={!find(weapons, ['id', aspd.weaponId]).lefthand}>
            <Option key={100} value={100}></Option>
            <Option key={101} value={101}></Option>
            {usableLefthand.map(({ id, baseAspd }) => (
              <Option key={id} value={id}>{find(weapons, { id }).name}</Option>)
            )}
          </Select>
        </Col>
      </Row>
    </InputField>
    <InputField>
      <Label></Label>
      <InputNumber
        min={0}
        max={200}
        value={aspd.equipMod}
        onChange={updateAspdEquipMod} /> %
    </InputField>
    <InputField>
      <Label></Label>
      <InputNumber
        min={0}
        max={200}
        value={aspd.skillMod}
        onChange={updateAspdSkillMod}/> %
    </InputField>
    <InputField>
      <Label>()</Label>
      <InputNumber
        min={0}
        max={20}
        value={aspd.equipFixed}
        onChange={updateAspdEquipFixed}/>
    </InputField>
    <InputField>
      <RadioGroup value={aspd.potionMod} onChange={({ target }) => updateAspdPotionMod(target.value)}>
        {aspdUpPotions.map(({ key, name, aspdUp }) => (
          <RadioButton key={key} value={aspdUp}>{name}</RadioButton>
        ))}
      </RadioGroup>
    </InputField>
  </Card>
);

const mapStateToProps = ({ job, stats, aspd }) => ({
  usableWeapons: find(aspdTable, ['job', job[1]]).weapons,
  usableLefthand: find(aspdTable, ['job', job[1]]).lefthand,
  stats,
  aspd,
});

const mapDispatchToProps = dispatch => bindActionCreators({
  updateAspdWeaponId,
  updateAspdLefthandId,
  updateAspdEquipMod,
  updateAspdEquipFixed,
  updateAspdSkillMod,
  updateAspdPotionMod,
}, dispatch);

export default connect(mapStateToProps, mapDispatchToProps)(AspdSetting);



